import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import PermitTypeTable from './Tables/PermitTypeTable';
import PermitTypeModal from './Modals/PermitTypeModal';
import RMSModuleSearch from '../../SearchComponents/RMSModuleSearch/RMSModuleSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class PermitType extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ID: 0,
            PERMITNAME: '',
            RMSMODULESID: 0,
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,

            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateTypes();

    }

    populateData() {
        const getdata = `
            query{
                PermitTypes{
                    ID
                    PERMITNAME
                    RMSMODULESID
                    RMSMODULES{
                        ID
                        MODULENAME
                    }


                    CREATEDBYID
                    LASTMODIFIEDBYID
                    DATECREATED
                    DATELASTMODIFIED
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.PermitTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                Modules  {
                    ID,
                    MODULENAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchdata: data.data.Modules
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to RMS Module Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
        mutation{
            addPermitType(
                PERMITNAME:"${this.state.PERMITNAME}",
                RMSMODULESID:${this.state.RMSMODULESID},

                CREATEDBYID:${this.state.CREATEDBYID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATECREATED:"${this.state.DATECREATED}",
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;
        console.log(form);
        const formEdit = `
        mutation{
            updatePermitType(
                ID:${this.state.ID},
                PERMITNAME:"${this.state.PERMITNAME}",
                RMSMODULESID:${this.state.RMSMODULESID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deletePermitType(
                ID:${this.state.ID}
                ){
                ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {
        //madhavi
        this.setState({
            RMSMODULESID: e.ID,
            MODULENAME: e.MODULENAME,
            opensearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            ID: e.ID,
            PERMITNAME: e.PERMITNAME,
            RMSMODULESID: e.RMSMODULESID,
            MODULENAME: e.RMSMODULES.MODULENAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ID: e.ID,
            PERMITNAME: e.PERMITNAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            PERMITNAME: '',
            RMSMODULESID: '',
            MODULENAME:''   
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.PERMITNAME.toString().toLowerCase().startsWith(value.toLowerCase()) 


                let includesCondition =
                    item.PERMITNAME.toString().toLowerCase().includes(value.toLowerCase())  


                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.MODULENAME.toString().toLowerCase().startsWith(value.toLowerCase()) 

                let includesCondition =
                    item.MODULENAME.toString().toLowerCase().includes(value.toLowerCase()) 

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, value, filteredData, searchdata, searchvalue } = this.state
        return (
            <>
                <Helmet><title>Permit Type</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Permit Type"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Permit Type</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">
                                    {/* //madhavi */}
                                    <RMSModuleSearch
                                        handleSearchRMSModule={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <PermitTypeTable
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <PermitTypeModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}
                                        // madhavi
                                        RMSModuleID={this.state.RMSMODULESID}
                                        handleChangeRMSModuleID={e => this.setState({ RMSMODULESID: e.target.value })}

                                        ModuleName={this.state.MODULENAME}
                                        handleChangeModuleName={e => this.setState({ MODULENAME: e.target.value })}

                                        PermitName={this.state.PERMITNAME}
                                        changePermitName={e => this.setState({ PERMITNAME: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(PermitType));