import React from 'react';
import {
    Button, Modal, Form, InputGroup
} from 'react-bootstrap';

// import { 
//     InputGroup,
//     InputGroupAddon,
//     Input, Col, Row
//     //InputGroupText,
// } from 'reactstrap';

import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';



class PermitTypeModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                 
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Permit Type
                        </Modal.Title>
                    </Modal.Header>

                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                           
                                <Form.Group controlId="PermitName">
                                    <Form.Label>Permit Name</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Permit Name" value={this.props.PermitName} onChange={this.props.changePermitName} required />
                                </Form.Group>
                            
                            
                                <Form.Group controlId="AdminType">
                                    <Form.Label>RMS Module ID</Form.Label>
                                    <InputGroup>
                                        <Form.Control value={this.props.RMSModuleID} onChange={this.props.handleChangeRMSModuleID} hidden />
                                        <Form.Control
                                            type="text"
                                            value={this.props.ModuleName}
                                            onChange={this.props.handleChangeModuleName}
                                            placeholder="Search Module Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                            readOnly
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                    </InputGroup>
                                </Form.Group>
                            

                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Permit Type
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.PermitName} onChange={this.props.changePermitName} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default PermitTypeModal;